import React, { useState } from 'react';
import { useDropzone } from 'react-dropzone';
import * as XLSX from 'xlsx';
import Plot from 'react-plotly.js';

// Hex 색상 코드를 RGBA로 변환하는 함수
const hexToRgba = (hex, alpha) => {
  const r = parseInt(hex.slice(1, 3), 16);
  const g = parseInt(hex.slice(3, 5), 16);
  const b = parseInt(hex.slice(5, 7), 16);
  return `rgba(${r}, ${g}, ${b}, ${alpha})`;
};

const App = () => {
  const [data, setData] = useState(null);
  const [chartData1, setChartData1] = useState([]);
  const [chartData2, setChartData2] = useState([]);
  const [showDropzone, setShowDropzone] = useState(true);
  const [initialFollowupData, setInitialFollowupData] = useState([]);
  const [departmentData, setDepartmentData] = useState([]);
  const [initialFollowupExpenseData, setInitialFollowupExpenseData] = useState([]);
  const [departmentExpenseData, setDepartmentExpenseData] = useState([]);

  const onDrop = (acceptedFiles) => {
    const file = acceptedFiles[0];
    const reader = new FileReader();

    reader.onload = (event) => {
      const binaryStr = event.target.result;
      const workbook = XLSX.read(binaryStr, { type: 'binary' });
      const sheetName = workbook.SheetNames[0];
      const sheet = workbook.Sheets[sheetName];
      let jsonData = XLSX.utils.sheet_to_json(sheet);
      jsonData = jsonData.filter(row => row['차트번호'] !== undefined && row['차트번호'] !== null && row['차트번호'] !== '');

      setData(jsonData);
      processData(jsonData);
      setShowDropzone(false);
    };

    reader.readAsBinaryString(file);
  };

  const processData = (data) => {
    const patientsPerHour = {};
    const expensesPerHour = {};
    const initialFollowupCounts = { 초진: 0, 재진: 0 };
    const initialFollowupExpenses = { 초진: 0, 재진: 0 };
    const departmentCounts = {};
    const departmentExpenses = {};

    data.forEach((row) => {
      const checkInTime = new Date(row['접수시간']);
      const expense = parseInt(row['총진료비'].replace(/,/g, ''), 10);
      const hour = checkInTime.getHours();
      const visitType = row['진료구분'];
      const department = row['진료분야'];

      if (!patientsPerHour[hour]) {
        patientsPerHour[hour] = 0;
        expensesPerHour[hour] = 0;
      }

      patientsPerHour[hour] += 1;
      expensesPerHour[hour] += expense;
      

      // 초진/재진 카운트 및 금액
      initialFollowupCounts[visitType] += 1;
      initialFollowupExpenses[visitType] += expense;

      // 진료분야 카운트 및 금액
      if (!departmentCounts[department]) {
        departmentCounts[department] = 0;
        departmentExpenses[department] = 0;
      }
      departmentCounts[department] += 1;
      departmentExpenses[department] += expense;
    });

    const formattedData1 = Object.keys(patientsPerHour).map((hour) => ({
      hour: `${hour}:00`,
      patients: patientsPerHour[hour],
    }));

    const formattedData2 = Object.keys(expensesPerHour).map((hour) => ({
      hour: `${hour}:00`,
      expenses: expensesPerHour[hour] / 10000,  // Convert to units of 만 (10,000)
    }));


    const formattedInitialFollowupData = Object.keys(initialFollowupCounts).map((type) => ({
      type,
      count: initialFollowupCounts[type],
    }));

    const formattedInitialFollowupExpenseData = Object.keys(initialFollowupExpenses).map((type) => ({
      type,
      expense: initialFollowupExpenses[type] / 10000,  // Convert to units of 만 (10,000)
    }));

    const formattedDepartmentData = Object.keys(departmentCounts).map((dept) => ({
      department: dept,
      count: departmentCounts[dept],
    }));

    const formattedDepartmentExpenseData = Object.keys(departmentExpenses).map((dept) => ({
      department: dept,
      expense: departmentExpenses[dept] / 10000,  // Convert to units of 만 (10,000)
    }));


    setChartData1(formattedData1);
    setChartData2(formattedData2);
    setInitialFollowupData(formattedInitialFollowupData);
    setInitialFollowupExpenseData(formattedInitialFollowupExpenseData);
    setDepartmentData(formattedDepartmentData);
    setDepartmentExpenseData(formattedDepartmentExpenseData);
  };

  const { getRootProps, getInputProps } = useDropzone({ onDrop });
  

  return (  <div style={styles.container}>
    {showDropzone && (
      <div {...getRootProps()} style={styles.dropzone}>
        <input {...getInputProps()} />
        <p>엑셀 파일을 여기로 드래그하거나 클릭하여 파일을 선택하세요.</p>
      </div>
    )}

    {chartData1.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: chartData1.map((d) => d.hour),
              y: chartData1.map((d) => d.patients),
              type: 'scatter',
              mode: 'lines+markers',
              marker: { color: hexToRgba('#A926FC', 0.5) }, // Blue with 50% opacity
            },
          ]}
          layout={{ 
            title: '시간당 환자 수', 
            xaxis: { title: '시간' }, 
            yaxis: { title: '환자 수' },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
    {chartData2.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: chartData2.map((d) => d.hour),
              y: chartData2.map((d) => d.expenses),
              type: 'scatter',
              mode: 'lines+markers',
              marker: { color: hexToRgba('#6326FC', 0.5) }, // Orange with 50% opacity
            },
          ]}
          layout={{ 
            title: '시간당 총 진료비 (단위: 만 원)', 
            xaxis: { title: '시간' }, 
            yaxis: { 
              title: '총 진료비 (₩)', 
              tickprefix: '₩',
              tickformat: ',.0f',  // Format ticks as integers
            },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
    {initialFollowupData.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: initialFollowupData.map((d) => d.type),
              y: initialFollowupData.map((d) => d.count),
              type: 'bar',
              marker: { color: hexToRgba('#2673FC', 0.5) }, // Blue with 50% opacity
            },
          ]}
          layout={{ 
            title: '초진/재진 별 환자 수', 
            xaxis: { title: '진료구분' }, 
            yaxis: { title: '환자 수' },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
    {initialFollowupExpenseData.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: initialFollowupExpenseData.map((d) => d.type),
              y: initialFollowupExpenseData.map((d) => d.expense),
              type: 'bar',
              marker: { color: hexToRgba('#EF26FC', 0.5) }, // Orange with 50% opacity
            },
          ]}
          layout={{ 
            title: '초진/재진 별 총 진료비 (단위: 만 원)', 
            xaxis: { title: '진료구분' }, 
            yaxis: { 
              title: '총 진료비 (₩)', 
              tickprefix: '₩',
              tickformat: ',.0f',  // Format ticks as integers
            },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
    {departmentData.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: departmentData.map((d) => d.department),
              y: departmentData.map((d) => d.count),
              type: 'bar',
              marker: { color: hexToRgba('#9F7AFC', 0.5) }, // Blue with 50% opacity
            },
          ]}
          layout={{ 
            title: '진료분야 별 환자 수', 
            xaxis: { title: '진료분야' }, 
            yaxis: { title: '환자 수' },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
    {departmentExpenseData.length > 0 && (
      <div style={styles.plotContainer}>
        <Plot
          data={[
            {
              x: departmentExpenseData.map((d) => d.department),
              y: departmentExpenseData.map((d) => d.expense),
              type: 'bar',
              marker: { color: hexToRgba('#FC2696', 0.5) }, // Orange with 50% opacity
            },
          ]}
          layout={{ 
            title: '진료분야 별 총 진료비 (단위: 만 원)', 
            xaxis: { title: '진료분야' }, 
            yaxis: { 
              title: '총 진료비 (₩)', 
              tickprefix: '₩',
              tickformat: ',.0f',  // Format ticks as integers
            },
            margin: { t: 50, l: 50, r: 50, b: 50 }
          }}
          config={{
            displayModeBar: true,
            responsive: true
          }}
        />
      </div>
    )}
  </div>
);
};

const styles = {
  container: {
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    overflowY: 'auto', // 세로 스크롤 활성화
  },
  dropzone: {
    width: '25%',
    paddingTop: '15%',
    paddingBottom: '15%',
    border: '2px dashed #0087F7',
    cursor: 'pointer',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    textAlign: 'center',
  },
  plotContainer: {
    width: '90%', // 필요에 따라 조정
    height: '60vh', // 화면 크기에 맞게 조정
    marginBottom: '20px', // 플롯 사이의 간격
    display: 'flex',
    justifyContent: 'center',
  },
};

export default App;
